import CodeMirror from '@uiw/react-codemirror';
import { javascript } from '@codemirror/lang-javascript';
import { oneDark } from '@codemirror/theme-one-dark';
import React, { CSSProperties, useCallback, useMemo, useRef } from 'react';
import { EditorView } from '@codemirror/view';

interface IProps {
  value: string;
  onChange: (e: any, value: string) => void;
  id?: string;
  name?: string;
  style?: CSSProperties;
  className?: string;
}

function CodeEditor(props: IProps) {
  
  const { id, value, onChange, name, style } = props;

  const handleChange = useCallback(val => {
    const event = {
      target: {
        name,
        value: val,
      }
    };
    onChange(event, val);
  }, [onChange, name]);

  const customTheme = useMemo(() => EditorView.theme({
    "&": {
      fontSize: "12px" // 基础字体大小
    },
    ".cm-content": {
      fontFamily: "'JetBrains Mono', monospace",
      fontSize: "10px" // 内容区字体大小
    },
    ".cm-gutters": {
      fontSize: "8px" // 行号区字体大小
    }
  }), []);

  return (
    <div
      style={style}
    >
      <CodeMirror
        id={id}
        value={value}
        extensions={[javascript({ jsx: true }), customTheme]}
        onChange={handleChange}
        // theme={oneDark}
      />
    </div>
  );
}

export default CodeEditor;