import React from "react";
import { useActionStore } from "../action";
import useCanvasStore from "../canvas/store";
import { List, ListItem, ListItemButton, ListItemText, Typography, IconButton, Menu, MenuItem, Divider } from '@mui/material';
import { map, get } from 'lodash-es';
import styles from './index.css';
import { ActionType } from "../constant";
import { Event, FormItem, FormItemType } from "../type";
import { FormRender } from "../form";
import { useSchemaStore } from "../schema";
import useComponentStore from "../components/store";
import { useCallback } from "react";
import { useState } from "react";
import { useRef } from "react";
import Popper from "../builtIn/components/Popper";
import { useEffect } from "react";
import { forwardRef } from "react";
import { useImperativeHandle } from "react";
import MoreHorizOutlinedIcon from '@mui/icons-material/MoreHorizOutlined';
import { useMemo } from "react";
import { getComponentMethods, getMethodOptions } from "../action/methods";
import { getSchemaById } from "../misc";
import { useShallowMemo } from "../hooks/common";

export const actionOptions = [
  {
    value: ActionType.CONTROL_COMPONENT,
    label: '控制组件',
  },
  {
    value: ActionType.RUN_SCRIPT,
    label: '运行脚本',
  },
  {
    value: ActionType.NOTIFY,
    label: '通知',
  },
  {
    value: ActionType.NAVIGATION,
    label: '跳转',
  }
];

const actionOptionsSchema: Record<string, FormItem[]> = {
  [ActionType.RUN_SCRIPT]: [
    {
      type: FormItemType.EDITOR,
      field: 'action.options.sourceCode',
      label: '代码',
    }
  ],
  [ActionType.NOTIFY]: [
    {
      type: FormItemType.INPUT,
      field: 'action.options.notifyContent',
      label: '通知内容',
      defaultValue: '这是一条消息通知',
    },
    {
      type: FormItemType.SELECT,
      field: 'action.options.notifyVariant',
      label: '通知类型',
      // 'default' | 'error' | 'success' | 'warning' | 'info'
      options: [
        {
          value: 'default',
          label: '默认',
        },
        {
          value: 'error',
          label: '错误',
        },
        {
          value: 'success',
          label: '成功',
        },
        {
          value: 'warning',
          label: '警告',
        },
        {
          value: 'info',
          label: '通知',
        }
      ]
    },
    {
      type: FormItemType.SWITCH,
      field: 'action.options.hideIconVariant',
      label: '隐藏图标',
    }
  ],
  [ActionType.NAVIGATION]: [
    {
      type: FormItemType.INPUT,
      field: 'action.options.navigationLink',
      label: '跳转链接',
    },
    {
      type: FormItemType.SELECT,
      field: 'action.options.navigationType',
      label: '跳转方式',
      options: [
        {
          value: '_blank',
          label: '新标签页',
        },
        {
          value: '_self',
          label: '当前页面',
        },
        {
          value: '_parent',
          label: '父框架',
        },
        {
          value: '_top',
          label: '顶层框架',
        }
      ]
    },
    {
      type: FormItemType.EDITOR,
      field: 'action.options.navigationFeature',
      label: '特性参数',
    }
  ]
}

const getOptionsSchema = ({ event }) => {
  const { action } = event;
  if (!action) {
    return [];
  }
  if (actionOptionsSchema[action?.type]) {
    return actionOptionsSchema[action?.type];
  }

  if (action.type === ActionType.CONTROL_COMPONENT) {
    const { plainSchema } = useSchemaStore.getState();
    return [
      {
        type: FormItemType.SELECT,
        field: 'action.options.target',
        label: '目标组件',
        options: Object.keys(plainSchema).map(id => ({ value: id, label: id })).filter(({ value }) =>{
          return getMethodOptions(plainSchema[value].type).length;
        }),
        onChange: (e, formik) => {
          formik.setFieldValue('action.options.method', '');
        }
      },
      {
        type: FormItemType.SELECT,
        field: 'action.options.method',
        label: '调用方法',
        hidden: '{{!formValues.action.options.target}}',
        options: (values) => {
          const target = get(values, 'action.options.target');
          if (!target) {
            return [];
          }
          return getMethodOptions(plainSchema[target].type);
        }
      },
      (values) => {
        const target = get(values, 'action.options.target');
        const method = get(values, 'action.options.method');
        console.log('here1');
        if (!target || !method) {
          return [];
        }
        const schema = getSchemaById(target);
        const extraOptionSchema = schema.methods.find(({ type }) => type === method).optionSchema;
        const prefix = 'action.options.';
        return map(extraOptionSchema, option => ({
          ...option,
          field: prefix + option.field,
        }));
      }
    ]
  }

  return [];
}

const assembleFormSchema = ({ event, eventOptions }) => {

  if (!event) {
    return {
      items: [],
    }
  }

  const optionSchema = getOptionsSchema({ event });

  const commonSchema: FormItem[] = [
    {
      type: FormItemType.SELECT,
      field: 'type',
      label: '事件类型',
      options: eventOptions,
    },
    {
      type: FormItemType.SELECT,
      field: 'action.type',
      label: '动作',
      options: actionOptions,
    },
    {
      key: 'optionDivider',
      render: (formItem, formik) => {
        return (
          <Divider>
            <Typography variant="caption">
              {`${actionOptions.find(opt => opt.value === event.action?.type)?.label}选项`}
            </Typography>
          </Divider>
        )
      }
    }
  ]

  return {
    items: [
      ...commonSchema,
      ...optionSchema,
    ]
  }
}

export const getDefaultEvent = (componentType: string): Event | null => {
  const { schema } = useComponentStore.getState()[componentType] || {};
  
  const eventType = schema?.events?.[0].type;

  if (!eventType) {
    return null;
  }

  return {
    type: eventType,
    action: {
      type: ActionType.NOTIFY,
      options: {
        sourceCode: 'console.log("hello")',
        notifyContent: '这是一条消息通知',
        notifyVariant: 'info',
        navigationLink: 'https://example.com',
        navigationType: '_blank',
        navigationFeature: JSON.stringify({
          width: 600,
          height: 400,
          scrollbars: 'yes',
          resizable: 'yes',
        }, null, 2),
      }
    }
  }
}

const ActionConfigPanel = ({ id, eventId }) => {
  const event = useActionStore(state => {
    if (!eventId) {
      return null;
    }
    const evt = state.events[id]?.find(event => event.id === eventId)
    return evt;
  });
  const updateEvent = useActionStore(state => state.updateEvent);
  const schemaNode = useSchemaStore(state => state.plainSchema[id]);
  const componentInfo = useComponentStore(state => state[schemaNode?.type]);

  const componentSchema = componentInfo.schema;

  const eventOptions = map(componentSchema.events, ({ type, name }) => ({ value: type, label: name }));

  const formSchema = useShallowMemo(() => {
    return assembleFormSchema({ event, eventOptions });
  }, [event, eventOptions]);

  const onChange = useCallback((values) => {
    if (values.action.callback) {
      delete values.action.callback;
    }
    updateEvent(id, eventId, values);
  }, [id, eventId]);

  return (
    <div className={styles['action-config-panel']}>
      <FormRender
       initialValues={event} 
       schema={formSchema}
       onChange={onChange}
       size="small"
      />
    </div>
  )
}

const EventList = forwardRef((props, ref) => {
  const selected = useCanvasStore(state => state.selected);
  const events = useActionStore(state => state.events[selected]);
  const addEvent = useActionStore(state => state.addEvent);
  const [open, setOpen] = useState(false);
  const anchorElRef = useRef<HTMLElement>(null);
  const [eventId, setEventId] = useState('');
  const schemaNode = useSchemaStore(state => state.plainSchema[selected]);
  const componentInfo = useComponentStore(state => state[schemaNode.type]);
  const menuAnchorElRef = useRef<HTMLElement>(null);
  const [menuOpen, setMenuOpen] = useState(false);
  const deleteEvent = useActionStore(state => state.deleteEvent);
  const menuEventIdRef = useRef('');
  
  const event = useMemo(() => {
    return events?.find(item => item.id === eventId);
  }, [events, eventId]);

  const eventDef = useMemo(() => {
    return componentInfo.schema.events.find(evt => evt.type === event?.type);
  }, [event, componentInfo]);

  const isEmpty = !events?.length;

  useEffect(() => {
    return () => {
      setOpen(false);
      setEventId('');
    }
  }, [selected]);

  useImperativeHandle(ref, () => (
    {
      openEventPanel: (eventId) => {
        anchorElRef.current = document.getElementById(`event-list-item-${eventId}`);
        setEventId(eventId),
        setOpen(true);
      }
    }
  ));

  const handleDelete = useCallback(() => {
    deleteEvent(selected, menuEventIdRef.current);
    setMenuOpen(false);
    menuEventIdRef.current = '';
  }, [selected]);

  return (
    <div className={styles['event-list']}>
      {isEmpty ? (
        <div 
          className={styles['event-list-empty']} 
          onClick={e => {
            const defaultEvent = getDefaultEvent(schemaNode.type);
            addEvent(selected, defaultEvent);
            const newEvent = useActionStore.getState().events[selected][0];
            anchorElRef.current = e.currentTarget.parentElement;
            setEventId(newEvent.id);
            setOpen(true);
          }}>
          <Typography>add event</Typography>
        </div>
      ) : (
      <List sx={{ p: 0 }}>
        {map(events, event => {
          const { id, type, action } = event;
          const eventDefinition = componentInfo.schema.events.find(evt => evt.type === type);
          const actionDefinition = actionOptions.find(act => act.value === action.type);
          return (
            <ListItem 
              key={id} 
              sx={{ p: 0 }} id={`event-list-item-${id}`} 
              secondaryAction={
                <IconButton 
                  edge='end'
                  size="small" 
                  sx={{ borderRadius: '3px' }} 
                  onClick={(e) => {
                    menuAnchorElRef.current = e.currentTarget;
                    menuEventIdRef.current = id;
                    setMenuOpen(true);
                    setOpen(false);
                    // setEventId('');
                  }}
                >
                  <MoreHorizOutlinedIcon sx={{ fontSize: '14px' }} />
                </IconButton>
              }
            >
              <ListItemButton
                sx={{ pt: 0, pb: 0 }}
                onClick={e => {
                  setEventId(id);
                  setOpen(true);
                  anchorElRef.current = e.currentTarget;
                }}
              >
                <ListItemText primary={eventDefinition.name} />
                <ListItemText sx={{ color: '#a1a1a1' }} primary={actionDefinition?.label} />
              </ListItemButton>
            </ListItem>
          )
        })}
      </List>
      )}
      <Popper
        open={open}
        anchorEl={anchorElRef.current!}
        placement="left"
        onOpenChange={isOpen => setOpen(isOpen)}
        title={
            eventDef?.name + '事件配置'
        }
      >
        <ActionConfigPanel
          id={selected}
          eventId={eventId}
        />
      </Popper>
      <Menu
        open={menuOpen}
        anchorEl={menuAnchorElRef.current}
        onClose={() => {
          menuEventIdRef.current = '';
          setMenuOpen(false);
        }}
      >
        <MenuItem 
          sx={{ color: 'red' }}
          onClick={handleDelete}
        >
          删除
        </MenuItem>
      </Menu>
    </div>
  )
})

export default EventList;