import React from "react";
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';

import { BrowserRouter, Routes, Route, useLocation } from "react-router";
import Editor from "./editor";
import Previewer from "./previewer";
import { StrictMode } from "react";
import { AppBar, Toolbar, IconButton, Typography, Tooltip } from "@mui/material";
import MenuIcon from '@mui/icons-material/Menu';
import PlayIcon from '@mui/icons-material/PlayArrow';
import { SnackbarProvider } from 'notistack';
import { preview } from "./misc";

const Platform = (props) => {

  const { children } = props;

  const location = useLocation();

  return (
    <SnackbarProvider maxSnack={3}>
      <AppBar position="static">
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            LOW_CODE
          </Typography>
          {/* <Button color="inherit">Login</Button> */}
          {location.pathname !== '/preview' && <Tooltip
            title='预览' 
            placement="bottom" 
            arrow
            slotProps={{
              popper: {
                modifiers: [
                  {
                    name: 'offset',
                    options: {
                      offset: [0, -20]
                    }
                  }
                ]
              }
            }}
          >
            <IconButton
              color="inherit"
              size="large"
              sx={{ mr: 3 }}
              onClick={preview}
            >
              <PlayIcon />
            </IconButton>
          </Tooltip>}
        </Toolbar>
      </AppBar>
      <div>
        {children}
      </div>
    </SnackbarProvider>
  )
}

const LowCode = () => {

  return (
    <StrictMode>
      <BrowserRouter>
        <Platform>
          <Routes>
            <Route index element={<Editor />} />
            <Route path="preview" element={<Previewer />} />
          </Routes>
        </Platform>
      </BrowserRouter>
    </StrictMode>
  )
}

export default () => {
  return <DndProvider backend={HTML5Backend}><LowCode /></DndProvider>;
} 