import React from "react";
import styles from './index.scss';
import { Popper as MUIPopper, Fade, Typography } from '@mui/material';
import CloseOutlined from '@mui/icons-material/CloseOutlined';
import { ReactNode } from "react";
import { useState } from "react";
import { useEffect } from "react";
import type { PopperPlacementType } from '@mui/material/Popper/BasePopper.types';
import { useMemo } from "react";

interface IProps {
  open: boolean;
  anchorEl?: HTMLElement;
  title?: ReactNode;
  showClose?: boolean;
  transition?: boolean;
  children?: ReactNode;
  placement?: PopperPlacementType;
  onOpenChange?: (open: boolean) => any;
}

const Popper = (props: IProps) => {
  const { open, transition = true, title, showClose = true, anchorEl, placement, onOpenChange, children } = props;

  return (
    <MUIPopper
      open={open}
      transition={transition}
      anchorEl={anchorEl}
      placement={placement}
      sx={{ zIndex: 20 }}
    >
      {({ TransitionProps }) => (
        <Fade {...TransitionProps} timeout={350}>
          <div className={styles['popper-container']}>
            {(title || showClose) && (
              <div className={styles['popper-title']}>
                {title && <Typography variant="subtitle2" sx={{ color: '#777777' }}>{title}</Typography>}
                {showClose && <span className={styles['popper-close-icon']} 
                  onClick={() => { 
                    onOpenChange?.(false);
                  }}>
                  <CloseOutlined fontSize="inherit" />
                </span>}
              </div>
            )}
            <div className={styles['popper-body']}>
              {children}
            </div>
          </div>
        </Fade>
      )}
    </MUIPopper>
  )
}

export default Popper;