import { useEffect } from "react";
import { CanvasPlugin } from "../../type";
import evtManager from "../../event";
import { EventType, GRID_ROW_HEIGHT } from "../../constant";
import { useSchemaStore } from "../../schema";
import { throttle } from 'lodash-es';

class AdaptivePlugin implements CanvasPlugin {

  heightIncreaseStep = 5;

  distanceToIncrease = GRID_ROW_HEIGHT;

  render() {

    const updateSchemaNode = useSchemaStore(state => state.updateNode);

    useEffect(() => {
      const draggingHandler = throttle((evt) => {
        const canvasRootElement = document.getElementById('canvas-root');
        if (!canvasRootElement) {
          return;
        }
        const canvasRootRect = canvasRootElement.getBoundingClientRect();
        
        const { item, coordi } = evt;
        
        const componentBottom = coordi.y + item.layout.height * GRID_ROW_HEIGHT;

        if (componentBottom + this.distanceToIncrease >= canvasRootRect.height) {
          const nextHeight = canvasRootRect.height + this.heightIncreaseStep;
          updateSchemaNode('CanvasRoot', { style: { height: nextHeight } });
          canvasRootElement.scrollTo({ top: canvasRootElement.scrollHeight });
        }
      }, 50);
      evtManager.on(EventType.DRAGGING, draggingHandler);

      return () => {
        evtManager.off(EventType.DRAGGING, draggingHandler);
      }
    }, []);
    return null;
  }
};

export default AdaptivePlugin;