import React from "react";
import useCanvasStore from "../canvas/store"
import styles from './index.css';
import { useSchemaStore } from "../schema";
import useComponentStore from "../components/store";
import widgets from "./widgets";
import { useStatesStore } from "../states";
import { useCallback } from "react";
import EventList, { getDefaultEvent } from "./eventList";
import { Breadcrumbs, Typography, Link, Box } from '@mui/material';
import AddOutlinedIcon from '@mui/icons-material/AddOutlined';
import { useActionStore } from "../action";
import { useRef } from "react";
import EmptySvg from '../assets/images/no-data.svg';

const TopBreadcrumbs = () => {
  const selected = useCanvasStore(state => state.selected);

  return (
    <Breadcrumbs sx={{ background: 'rgba(25, 118, 210, .8)', padding: '3px 5px', borderRadius: '2px'  }}>
      <Link
        underline="hover"
        color="#fff"
      >
        {selected}
      </Link>
    </Breadcrumbs>
  )
}

const PropsPanelItem = (props) => {
  const { widget: widgetType,  _key: key, name, schemaNode, widgetProps } = props;
  const Widget = widgets[widgetType] || (() => 'not support yet');
  const { id } = schemaNode;
  const stateValue = useSchemaStore(state => state.plainSchema[id].state[key]);
  const setState = useStatesStore(state => state.setState);
  const updateSchemaNode = useSchemaStore(state => state.updateNode);

  const onChange = useCallback((value) => {
    setState(id, { [key]: value });
    updateSchemaNode(id, { state: { [key]: value } })
  }, [setState, key, updateSchemaNode, id]);

  return (
    <div className={styles['props-panel-item']}>
      <div className={styles['props-panel-item-label']}>
        <Typography variant="body2">{name}</Typography>
      </div>
      <div className={styles['props-panel-item-widget']}>
        <Widget value={stateValue} onChange={onChange} {...widgetProps} />
      </div>
    </div>
  )
}

const EmptyContent = props => {
  return (
    <Box
      display='flex'
      flexDirection='column'
      alignItems='center'
      justifyContent='center'
      rowGap='20px'
      height='300px'
    >
      <EmptySvg 
        width={120}
        preserveAspectRatio="xMidYMid meet"
      />
      <Typography
        variant="caption"
      >
        未选择组件
      </Typography>
    </Box>
  );
}

function renderItems(componentSchema, schemaNode) {
  const { attributes = [] } = componentSchema;

  return (
    <div className={styles['items-container']}>
      {attributes.map(attri => {
        const attriCopy = { ...attri, _key: attri.key };
        return (
          <PropsPanelItem  {...attriCopy} schemaNode={schemaNode} />
        )
      })}
    </div>
  )
}

const PanelSection = props => {

  const { children, title, onAdd } = props;

  return (
    <div className={styles['panel-section']}>
      <div className={styles['panel-section-title']}>
        <Typography variant="subtitle1">{title}</Typography>
        {onAdd && (<span className={styles['panel-section-title-add']} onClick={onAdd}>
          <AddOutlinedIcon fontSize="inherit" />
        </span>)}
      </div>
      <div className={styles['panel-section-content']}>
        {children}
      </div>
    </div>
  )
}

const PropsPanel = () => {
  const selectedId = useCanvasStore(state => state.selected);
  const schemaNode = useSchemaStore(state => state.plainSchema[selectedId]);

  const componentInfo = useComponentStore(state => state[schemaNode?.type]);

  const componentSchema = componentInfo?.schema;

  const showEventList = !!componentSchema?.events?.length

  const addEvent = useActionStore(state => state.addEvent);
  const getLatestEvent = useActionStore(state => state.getLatestEvent);

  const eventListRef = useRef<any>(null);

  return (
    <div className={styles['props-panel']}>
      <div className={styles['props-panel-inner']}>
        {selectedId ? (
          <>
            <TopBreadcrumbs />
            <PanelSection key='content' title='属性'>
              {renderItems(componentSchema, schemaNode)}
            </PanelSection>
            {showEventList && (
              <PanelSection 
                key='event' 
                title='事件' 
                onAdd={() => { 
                  addEvent(selectedId, getDefaultEvent(schemaNode.type));
                  const theEvent = getLatestEvent(selectedId);
                  setTimeout(() => {
                    eventListRef.current?.openEventPanel(theEvent.id);
                  })
                }}
              >
                <EventList ref={eventListRef} />
              </PanelSection>)
            }
          </>
        ) : <EmptyContent />}
      </div>
    </div>
  )
}

export default PropsPanel;