import { TextField } from "@mui/material";
import React from "react";
import { ComponentSchema, FormItemType } from "../../type";
import InputCover from '../thumbnails/Input.jpg';


const Input = props => {
  const { value, onChange, label, placeholder } = props;

  return (
    <TextField
      value={value} 
      onChange={onChange} 
      label={label}
      placeholder={placeholder} 
      fullWidth
      style={{
        height: '100%',
      }}
    />
  )
}

const schema: ComponentSchema = {
  type: 'Input',
  name: '输入框',
  attributes: [
    {
      key: 'value',
      name: '值',
      widget: 'input',
      defaultValue: '',
      widgetProps: {
        placeholder: '输入框值'
      }
    },
    {
      key: 'label',
      name: '标签',
      widget: 'input',
      defaultValue: '标签',
    },
    {
      key: 'placeholder',
      name: '占位文字',
      widget: 'input',
      defaultValue: '占位文字',
    },
  ],
  events: [
    {
      type: 'onChange',
      name: '值改变',
      bindState: 'value',
    }
  ],
  methods: [
    {
      type: 'setValue',
      name: '设置值',
      optionSchema: [
        {
          type: FormItemType.INPUT,
          field: 'Input_Value',
          label: '值',
        }
      ],
      handler: context => {
        const { api, self, options } = context;
        api.setStates(self, { value: options.Input_Value });
      }
    }
  ],
  layout: {
    width: 2,
    height: 10,
    minHeight: 8,
  },
  cover: InputCover,
}

Object.assign(Input, { schema });

export default Input;